import { ButtonHTMLAttributes, ReactNode } from 'react';
import {clsx} from 'clsx'
import { Slot } from '@radix-ui/react-slot';


export interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  children: ReactNode;
  asChild?: boolean;
  className?: string;
}

export function Button({children, asChild, className, ...rest}: ButtonProps){
  const Comp = asChild ? Slot : 'button'
  
  return (
    <Comp className={
      clsx('py-3 px-4 bg-cyan-500 roundedn font-semibold text-black text-sm w-full hover:bg-cyan-300 transition-all:200ms focus:ring-2 ring-white',
      
      className
      )
    }
      {...rest}
    >{children}</Comp>
  )
}